export interface IBase<T = unknown> {
    config: T;
    configErrors?: IConfigError[];
    friendlyName: string;
    id: string;
    typeName: string;
}

export interface IConfigError {
    id?: string;
    message: string;
    property?: string;
}

export interface IPageRoot {
    title: string;
    titleTemplate: string;
    faviconUrl: string;
    bodyClass: string;
    pageDescription: string;
    pageKeywords: string;
    canonicalUrl: string;
}
